@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background: linear-gradient(270deg,#2c5364,#203a43,#0f2027);
  font-family: 'Open Sans', sans-serif;
}

main {
    width: 100%;
    max-width: 960px;
    margin: 0 auto;
    color: white;
}

/*Resize the wrap to see the search bar change!*/
.wrap{
  width: 30%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.search {
    position: relative;
    display: flex;
    margin: 15px;
}

.search button {
    margin-right: 5px;
}

.searchTerm {
    width: 100%;
    border: 3px solid #00B4CC;
    border-right: none;
    padding: 5px;
    height: 20px;
    border-radius: 5px 0 0 5px;
    outline: none;
    color: #9DBFAF;
}
  
.searchTerm:focus{
    color: #00B4CC;
}
  
.searchButton {
    height: 36px;
    border: 1px solid #00B4CC;
    background: #00B4CC;
    text-align: center;
    color: #fff;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
    font-size: 20px;
}
.movie {
    display: flex;
    flex-direction: column;
    width: 33.33%;
    padding: 10px;
    box-sizing: border-box;
}

@media only screen and (max-width: 769px) {
    .movie {
        width: 50%;
    }
}

@media only screen and (max-width: 481px) {
    .movie {
        width: 100%;
    }
}

.movie-image {
    width: 100%;
    height: 360px;
}

.footer {
    background-color:beige;
    color: black;
}

.footer.expanded {
    height: 120px;
    overflow-y: scroll;
}
.movie-footer {
    display: flex;
    justify-content: space-between;
    padding: 5px;
}

.expand-button {
    cursor: pointer;
    width: 22px;
    height: 22px;
}

.movie-detail {
    text-align: left;
    margin-left: 5px;;
}
.App {
    text-align: center;
}

.movie-list {
    display: flex;
    flex-wrap: wrap;
}

.load-more {
    width: 100%;
    margin-top: 14px;
    padding: 20px 0;
    text-align: center;
    background-color: rgb(116, 250, 255);
    font-size: 14px;
    cursor: pointer;
}

