.movie {
    display: flex;
    flex-direction: column;
    width: 33.33%;
    padding: 10px;
    box-sizing: border-box;
}

@media only screen and (max-width: 769px) {
    .movie {
        width: 50%;
    }
}

@media only screen and (max-width: 481px) {
    .movie {
        width: 100%;
    }
}

.movie-image {
    width: 100%;
    height: 360px;
}

.footer {
    background-color:beige;
    color: black;
}

.footer.expanded {
    height: 120px;
    overflow-y: scroll;
}
.movie-footer {
    display: flex;
    justify-content: space-between;
    padding: 5px;
}

.expand-button {
    cursor: pointer;
    width: 22px;
    height: 22px;
}

.movie-detail {
    text-align: left;
    margin-left: 5px;;
}